body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

root {
  margin: 0;
}



.header{width:100%;background:darkorange;padding-bottom:10px}.header .info{width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center}

.spinner{margin:100px auto;width:40px;height:40px;position:fixed;top:50%;left:50%;z-index:3}.cube1,.cube2{background-color:#333;width:15px;height:15px;position:absolute;top:0;left:0;-webkit-animation:sk-cubemove 1.8s infinite ease-in-out;animation:sk-cubemove 1.8s infinite ease-in-out}.cube2{-webkit-animation-delay:-0.9s;animation-delay:-0.9s}@-webkit-keyframes sk-cubemove{25%{-webkit-transform:translateX(42px) rotate(-90deg) scale(0.5)}50%{-webkit-transform:translateX(42px) translateY(42px) rotate(-180deg)}75%{-webkit-transform:translateX(0px) translateY(42px) rotate(-270deg) scale(0.5)}100%{-webkit-transform:rotate(-360deg)}}@keyframes sk-cubemove{25%{transform:translateX(42px) rotate(-90deg) scale(0.5);-webkit-transform:translateX(42px) rotate(-90deg) scale(0.5)}50%{transform:translateX(42px) translateY(42px) rotate(-179deg);-webkit-transform:translateX(42px) translateY(42px) rotate(-179deg)}50.1%{transform:translateX(42px) translateY(42px) rotate(-180deg);-webkit-transform:translateX(42px) translateY(42px) rotate(-180deg)}75%{transform:translateX(0px) translateY(42px) rotate(-270deg) scale(0.5);-webkit-transform:translateX(0px) translateY(42px) rotate(-270deg) scale(0.5)}100%{transform:rotate(-360deg);-webkit-transform:rotate(-360deg)}}

.beer{display:flex;flex-direction:column;align-items:center;padding:7.5px;-webkit-transition:ease-in-out 0.2s;transition:ease-in-out 0.2s;background-color:white;box-shadow:0 0 10px -3px rgba(0,0,0,0.25);border-radius:5px;position:relative}.beer:hover{box-shadow:1px 1px 11px 3px rgba(68,68,68,0.9)}.beer .beer-image{width:80px;height:250px}.beer .beer-image img{width:100%;height:100%;cursor:pointer}.beer .star-icon{position:absolute;top:10px;right:10px;width:20px;height:20px;-webkit-transition:ease-in 0.2s;transition:ease-in 0.2s;cursor:pointer}.beer .highlight{width:20px;height:20px;cursor:pointer}

body{background-color:#ede9f2}.beerContainer{display:grid;margin-top:30px;grid-template-columns:repeat(auto-fill, minmax(200px, 1fr));grid-gap:20px;padding:0 25px 25px 25px;background-color:#ede9f2;-webkit-transition:ease-in-out 0.2s;transition:ease-in-out 0.2s}.beerContainer .selectField{position:absolute;top:195px;right:7.5px;-webkit-transition:ease-in-out 0.2s;transition:ease-in-out 0.2s}

.detailContainer{width:100%;height:100%;z-index:8;position:fixed;top:0;background-color:rgba(51,51,51,0.51);display:flex;justify-content:center;align-items:center;overflow:hidden;-webkit-transition:0.3s ease-in-out;transition:0.3s ease-in-out}.detailContainer .details{padding:20px 20px 0 20px;display:grid;grid-template-areas:"image title star " "image tagline tagline" "image descr   descr" "random random random";grid-gap:20px;width:80%;min-width:400px;height:80%;position:fixed;background:whitesmoke;box-sizing:content-box}.detailContainer .details span:hover{cursor:pointer}.detailContainer .details img{height:100%;grid-area:image;align-self:center}.detailContainer .details b{grid-area:title;align-self:center}.detailContainer .details h5{grid-area:tagline}.detailContainer .details p{grid-area:descr}.detailContainer .details img.star{position:absolute;grid-area:star;top:25px;right:25px;width:50px;height:50px;-webkit-transition:ease-in 0.2s;transition:ease-in 0.2s;cursor:pointer}.detailContainer .details img.highlight{position:absolute;grid-area:star;top:25px;right:25px;width:50px;height:50px;-webkit-transition:ease-in 0.2s;transition:ease-in 0.2s;cursor:pointer}.detailContainer .details .star{position:absolute;grid-area:star;top:25px;right:25px;width:50px;height:50px;-webkit-transition:0.3s ease-in-out;transition:0.3s ease-in-out;cursor:pointer}.detailContainer .details i{position:absolute;top:5px;right:5px}.detailContainer .random-container{display:flex;grid-area:random;-webkit-transition:ease-in-out 0.3s;transition:ease-in-out 0.3s}@media screen and (max-width: 750px){.detailContainer .random-container{display:none}}.detailContainer .random-container .random{display:grid;width:100%;padding-left:10px;padding-right:10px;-webkit-transition:0.3s ease-in-out;transition:0.3s ease-in-out;grid-gap:10px;grid-template-areas:"b img" "h5 img" "h5 img"}.detailContainer .random-container .random img{cursor:pointer;grid-area:img;-webkit-transition:0.3s ease-in-out;transition:0.3s ease-in-out}.detailContainer .random-container .random b{grid-area:b;-webkit-transition:0.3s ease-in-out;transition:0.3s ease-in-out;text-align:center}.detailContainer .random-container .random h5{grid-area:h5;-webkit-transition:0.3s ease-in-out;transition:0.3s ease-in-out;text-align:center}

#error{text-align:center}

nav{width:100%;top:0;position:-webkit-sticky;position:sticky;padding:auto 10px;height:50px;margin:0;background:darkorange;display:flex;flex-direction:row;-webkit-transition:ease-in-out 0.2s;transition:ease-in-out 0.2s;z-index:1}nav .link{-webkit-transition:ease-in 0.2s;transition:ease-in 0.2s;padding:10px;display:flex;margin:auto 10px;justify-content:center;align-items:center;text-decoration:none;border:none;background-color:transparent;font-size:20px;cursor:pointer;border-radius:5px}nav .link:hover{color:honeydew;background-color:rgba(178,46,0,0.37)}nav .link:focus{color:#333333;border:none}nav a{padding:10px}

.search-container{display:flex;margin-top:20px;justify-content:center;flex-direction:column;align-items:center}.search-container .search-field{width:290px;margin:5px;display:flex;justify-content:space-between}.search-container .search-field label{height:20px}.search-container .search-field input{height:20px}

